$bodyColor: "red";
$textColor: #000;
$warningColor: rgba(220, 53, 69, 0.4);
$okColor: rgba(40, 167, 69, 0.4);
body{
	color: $bodyColor;
}
/*---pdf page--*/
hr{
	margin-top: 5px;
	margin-bottom: 5px;
}	
.form-control{
	border-radius: 0;
}
.alert{
	padding: 5px;
	border-radius: 0;
}
.imageTexts .btn{
	min-width: 25px;
    border-radius: 50%;
    white-space: nowrap;
    padding: 2px;
    font-size: 13px;
    border: 1px solid rgba(0,0,0,0.3);
}
.imageParent{
	position: relative;
	z-index: 1;
	border: 3px solid #ccc;
    box-shadow: 0 0 15px rgba(0,0,0,0.2);	
}
.imageTexts{
	position: absolute;
	z-index: 2;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
    font-size: 14px;
    font-weight: bold;
    color: $textColor;	
}
.imageText{
	border: 1px solid transparent;
}
.imageText:hover{
	border: 1px dashed #ccc;
}
.imageText.warningText{	
	background: $warningColor;
}
.imageText.okText{
	background: $okColor;	
}
.imageTextFileldParent{
	z-index: 20;
	position: absolute;		
}
.imageTextFileld{
    padding: 0;
    border-radius: 0;
	background: transparent;	
    font-size: 14px;
    font-weight: bold;
    color: $textColor;	
	resize: both;
	width: 200px;
}
.imageTextFileld.form-control-warning{
	background: $warningColor;
}
.imageTextFileld.form-control-ok{
	background: $okColor;
}
.imageTextFileldButtons{
	position: absolute;
	z-index: 2;
    left: 100%;
    top: 0;
    white-space: nowrap;
}
.imageTextFileldMessageParent{
    position: absolute;
    left: 0;
    width: 220px;
}